* {
    margin: 0;
    padding: 0;
}

#drum-machine {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

#drum-pads {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(3, 1fr);
}

.drum-pad {
    border: 1px solid #000;
    padding: 30px;
}
.drum-pad.active {
    background-color: #ffcb47;
}

#display {
    text-align: center;
    padding: 20px;
}